@import "../1.base/colors.scss";
@import "../1.base/media.scss";
$section-body-height:25px;
footer{
    a{
        text-decoration: none!important;
    }
    a:hover{
        text-decoration: underline!important;
    }      
    ul{
        padding: 0;
        li{
            padding: 3px 0;
            .view-more{
                padding: 0;                
                color: $footer-down-right-links-color;
            }
            .view-more:hover{
                
                color: $footer-down-right-links-color;
            }
        }
    }    
    .footer-top{
        border-bottom: 1px solid $footer-top-border-color;
        background-color: $footer-top-background;
        padding: $section-body-height 0;
        
        a{
            color: $footer-top-links-color;
        }
        .footer-top-title{
            color: $footer-top-titles-color;
            
        }
    }
    .footer-down{
        text-align: center;
        background-color: $footer-down-background;
        padding: 30px 0 15px 0;
        .phone-link a, .footer-down-title, .social-links{
            display: inline-block;
            margin: 0 0 10px 0;
            font-size: 1.7em; 
            color: $footer-down-color!important;
        }     
        
        .social-links{
            .social-cf{
                margin: 8px;
                color: $footer-down-color;
                i{
                    font-size: 1.3em;
                }
            }
            .social-cf:hover{
                margin: 8px;
                color: $footer-down-right-links-color;
                
            }
        }
        .casafix-right{
             color: $footer-down-right-color;
             p{
                 padding: 10px 0;
                 margin: 0;
             }
             a{                 
                 color: $footer-down-right-links-color;
                 i{
                     padding: 0 3px;
                     color: $footer-down-right-color;
                 }
             }
             a:hover{
                
                color: $footer-down-right-links-color;
            }

        }
    }
}
@media (min-width: $screen-sm-min) {
    footer{
        $section-body-height:40px;        
        .footer-top{
            padding-top: $section-body-height;

        }
        .footer-down{
            padding-bottom: 15px;
        }
        
    }
}
